import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemText from "@material-ui/core/ListItemText";
import MenuItem from "@material-ui/core/MenuItem";
import Menu from "@material-ui/core/Menu";
import { apiRequest } from "../utils/helper";

const useStyles = makeStyles(theme => ({
  root: {
    width: "100%",
    maxWidth: 360,
    backgroundColor: theme.palette.background.paper
  }
}));

export default function MenuStrikePrice({
  strikeprice,
  strikeprice2,
  date,
  index,
  type
}) {
  const classes = useStyles();
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [selectedIndex, setSelectedIndex] = React.useState(1);
  const [options, setOptions] = React.useState([]);
  React.useEffect(() => {
    const authHandler = async () => {
      try {
        const userData = await apiRequest({
          url: "https://api.optionalgo.in:6001/v1/getstrikeprices",
          method: "post",
          bodyParams: {
            date: date,
            pointtype: type,
            stockname: index
          }
        });
        const { data, success } = userData;
        console.log(userData);
        if (success) {
          setOptions(data);
        }
      } catch (err) {}
    };
    authHandler();
  }, [date, index, type]);

  const handleClickListItem = event => {
    setAnchorEl(event.currentTarget);
  };

  const handleMenuItemClick = (event, index) => {
    setSelectedIndex(index);
    console.log(index);
    console.log(options[index]);
    if (strikeprice) strikeprice(options[index]);
    if (strikeprice2) strikeprice2(options[index]);
    setAnchorEl(null);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  if (date !== "" && index !== "") {
    return (
      <div className={classes.root}>
        <List component="nav" aria-label="Device settings">
          <ListItem
            button
            aria-haspopup="true"
            aria-controls="lock-menu"
            aria-label={"Select " + type + " Price"}
            onClick={handleClickListItem}
          >
            <ListItemText
              primary={"Select " + type + " Price"}
              secondary={options[selectedIndex]}
            />
          </ListItem>
        </List>
        <Menu
          id="lock-menu"
          anchorEl={anchorEl}
          keepMounted
          open={Boolean(anchorEl)}
          onClose={handleClose}
        >
          {options.map((option, index) => (
            <MenuItem
              key={option}
              selected={index === selectedIndex}
              onClick={event => handleMenuItemClick(event, index)}
            >
              {option}
            </MenuItem>
          ))}
        </Menu>
      </div>
    );
  } else {
    return <div></div>;
  }
}
